<template>
  <div class="packages-container">
    <Tabs value="name1">
      <TabPane label="" name="name1">
        <soft-ware></soft-ware>
      </TabPane>
      <TabPane label="" name="name2">
        <res-info></res-info>
      </TabPane>
      <TabPane label="" name="name3">
        <theme-info></theme-info>
      </TabPane>
    </Tabs>
    
  </div>
</template>
<script>
  import {saveAs} from 'file-saver'
  import axios from 'axios'
  
  import SoftWare from './SoftWare.vue'
  import ResInfo from './ResInfo.vue'
  import ThemeInfo from './ThemeInfo.vue'

 export default {
    data() {
      return {
        searchKey: '',
        cityList: [
          {
            value: 'beijing',
            label: ''
          },
          {
            value: 'shanghai',
            label: ''
          },
          {
            value: 'shenzhen',
            label: ''
          },
          {
            value: 'hangzhou',
            label: ''
          },
          {
            value: 'nanjing',
            label: ''
          },
          {
            value: 'chongqing',
            label: ''
          }
        ],
        model1: '',
        model2: '',
        columns1: [
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'age'
          },
          {
            title: '',
            key: 'address'
          }
        ],
        data1: [
          {
            name: '',
            age: 18,
            address: ''
          }
        ],
        tableData1: this.mockTableData1(),
        tableColumns1: [
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'status',
            render: (h, params) => {
              const row = params.row;
              const color = row.status === 1 ? 'blue' : row.status === 2 ? 'green' : 'red';
              const text = row.status === 1 ? '' : row.status === 2 ? '' : '';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
            title: '',
            key: 'portrayal',
            render: (h, params) => {
              return h('Poptip', {
                props: {
                  trigger: 'hover',
                  title: params.row.portrayal.length + '',
                  placement: 'bottom'
                }
              }, [
                h('Tag', params.row.portrayal.length),
                h('div', {
                  slot: 'content'
                }, [
                  h('ul', this.tableData1[params.index].portrayal.map(item => {
                    return h('li', {
                      style: {
                        textAlign: 'center',
                        padding: '4px'
                      }
                    }, item)
                  }))
                ])
              ]);
            }
          },
          {
            title: '',
            key: 'people',
            render: (h, params) => {
              return h('Poptip', {
                props: {
                  trigger: 'hover',
                  title: params.row.people.length + '',
                  placement: 'bottom'
                }
              }, [
                h('Tag', params.row.people.length),
                h('div', {
                  slot: 'content'
                }, [
                  h('ul', this.tableData1[params.index].people.map(item => {
                    return h('li', {
                      style: {
                        textAlign: 'center',
                        padding: '4px'
                      }
                    }, item.n + '' + item.c + '')
                  }))
                ])
              ]);
            }
          },
          {
            title: '',
            key: 'time',
            render: (h, params) => {
              return h('div', '' + params.row.time + '');
            }
          },
          {
            title: '',
            key: 'update',
            render: (h, params) => {
              return h('div', this.formatDate(this.tableData1[params.index].update));
            }
          }
        ]
      }
    },
    components: {
      SoftWare,
      ResInfo,
      ThemeInfo
    },
    methods: {
      mockTableData1() {
        let data = [];
        for (let i = 0; i < 10; i++) {
          data.push({
            name: '' + Math.floor(Math.random() * 100 + 1),
            status: Math.floor(Math.random() * 3 + 1),
            portrayal: ['', '', '', '', ''],
            people: [
              {
                n: '' + Math.floor(Math.random() * 100 + 1),
                c: Math.floor(Math.random() * 1000000 + 100000)
              },
              {
                n: '' + Math.floor(Math.random() * 100 + 1),
                c: Math.floor(Math.random() * 1000000 + 100000)
              },
              {
                n: '' + Math.floor(Math.random() * 100 + 1),
                c: Math.floor(Math.random() * 1000000 + 100000)
              }
            ],
            time: Math.floor(Math.random() * 7 + 1),
            update: new Date()
          })
        }
        return data;
      },
      formatDate(date) {
        const y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
      },
      changePage() {
        // 
        this.tableData1 = this.mockTableData1();
      },
      exportCDKey() {
//        let blob = new Blob(['hello,world'], {type: 'text'});
//        saveAs(blob, 'hello.txt');
        this.$refs.CDKeyTable.exportCsv({
          filename: ''
        });
      },
      
    }
  }
</script>
<style lang="scss" scoped>
  .packages-container {
    padding: 10px 10px 0 20px;
  }
</style>


