<template>
    <div style="width: 800px; height: 300px;" :id="domId"></div>
</template>
<script>
  import echarts from 'echarts'

  export default {
    props: ['domId'],
    mounted() {
      this.$nextTick(() => {
        var sortBar = echarts.init(document.getElementById(this.domId));
        var option = {
          color: ['#0fbea5'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 
              type: 'shadow'        // 'line' | 'shadow'
            }
          },
          legend: {
            data: ['']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true,
          },
          xAxis: {
            type: 'value'
          },
          yAxis: {
            type: 'category',
            data: ['', '', '', '', '', '', '']
          },
          series: [
            {
              name: '',
              type: 'bar',
              stack: '',
              label: {
                normal: {
                  show: true,
                  position: 'insideRight'
                }
              },
              data: [820, 832, 901, 934, 1290, 1330, 1320],
              barWidth: 10
            }
          ]
        };

        sortBar.setOption(option);
        window.addEventListener('resize', function () {
          sortBar.resize();
        });
      });
    }
  }
</script>


